import Config from "../config";

let headers = { "Content-Type": "application/json" };

const getHttp = (query, url) => {
    let token = localStorage.getItem('token');
    if(token) {
        headers = { "Content-Type": "application/json", "Authorization": "Bearer " + token};
    }else {
        headers = { "Content-Type": "application/json"};
    }
    let queries = "";
    Object.keys(query).forEach(function(key,index) {
        if(index == 0) {
            queries += "?key=" + query[key];
        }else{
            queries += "&key=" + query[key];
        }
    });
    return new Promise((resolve, reject) => {
        fetch(Config.BASE_URL + url + queries, { 
            method: "GET",
            headers: headers,
            referrerPolicy: 'no-referrer'
         }).then(async (res) => {
            if(res.ok){
                return res.json();
            }else{
                return res.text().then(text => {
                    throw Error(JSON.parse(text).message);
                });
            }
        }) .then((data) => resolve(data))
        .catch((err) => {
            reject(err);
        });
    });
}

const postHttp = (data, url) => {
    let token = localStorage.getItem('token');
    if(token) {
        headers = { "Content-Type": "application/json", "Authorization": "Bearer " + token};
    }else {
        headers = { "Content-Type": "application/json"};
    }
    return new Promise((resolve, reject) => {
        fetch(Config.BASE_URL + url, {
            method: "POST",
            headers: headers,
            referrerPolicy: 'no-referrer',
            body: JSON.stringify(data)
        }).then((res) => {
            if(res.ok){
                return res.json();
            }else{
                return res.text().then(text => {
                    throw Error(JSON.parse(text).message);
                });
            }
        })
        .then((data) => resolve(data))
        .catch((err) => reject(err));
    });
}

