
const state = () => ({
    tai: 0,
    xiu: 0,
    tongtai: 0,
    tongxiu: 0,
    result: null,
    history: [],
    coin: 0,
    error: "",
    secret: "",
    game_id: "",
    msg: "",
    balance: localStorage.getItem("user")? JSON.parse(localStorage.getItem("user")).balance : 0,
    win: 0
})

const getters = {}

const actions = {

    updateGameInfo({ commit }, data){
        commit("setGameInfo", data)
        return true;
    },

    async selectCoin ({ commit }, data) {
        commit("updateCoin", data.coin)
    },

    verify({ commit, state }) {
        if(state.coin == 0){
            commit("setError", "Vui long chon so tien")
            return false
        }
        return true
    },

    confirmUser({ commit}, data){
        commit("setConfirmData", data)
    },

    confirmTotal({ commit }, data){
        commit("setConfirmTotal", data)
    },

    updateResult({ commit }, data) {
        commit("setGameResult", data.result)
    },

    updateWin({ commit }, data){
        commit("setWin", data);
    },

    updateBalance({ commit }, data) {
        commit("setBalance", data)
    },

    goError({ commit }, data) {
        commit("setError", data.msg)
    }
}
  
  // mutations
const mutations = {
    setConfirmData(state, data) {
        state.tai = data.tai
        state.xiu = data.xiu
        state.balance = data.balance
    },

    setConfirmTotal(state, data) {
        state.tongtai = data.tai
        state.tongxiu = data.xiu
    },

    setGameInfo(state, data){
        state.tongtai = 0
        state.tongxiu = 0
        state.tai = 0
        state.xiu = 0
        state.result = 0
        state.secret = data.secret
        state.game_id = data.game_id
        state.msg = "Bat dau dat cuoc"
    },

    updateCoin(state, value) {
        state.coin = value;
    },

    setGameResult(state, result) {
        state.result = result
    },

    setWin(state, data){
        state.win = data.win
    },

    setBalance(state, data) {
        state.balance = data.balance
    },

    setError(state, error) {
        state.error = error
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}