import userApi from '../../api/users';

const state = () => ({
    messages: [],
    error: ""
})

const getters = {}

const actions = {
    async loadMessages ({ commit }) {
        try{
            const msgs = await userApi.loadMessages();
            commit("setMessagesResult", msgs);
            return true;
        }catch(err){
            commit("setError", err);
        }
        return false;
    },

    addMessage({ commit }, message) {
        commit("addMessageResult", message);
        return true;
    }
}
  
  // mutations
const mutations = {
    setMessagesResult(state, msgs) {
        state.messages = msgs;
    },

    addMessageResult(state, message) {
        state.messages.push(message);
    },

    setError (state, error) {
        state.error = error;
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}