import authApi from '../../api/auth';

const state = () => ({
    token: "",
    userId: localStorage.getItem("userId"),
    error: "",
    loading: false,
    login: localStorage.getItem("userId") != null && localStorage.getItem("userId") != "",
    logout: localStorage.getItem("userId") == null || localStorage.getItem("userId") == ""
})

const getters = {
}

const actions = {

    setLoading({ commit }) {
        commit("setLoading", true);
    },

    clearError({ commit }) {
        commit("setAuthError", "");
    },

    goLogout({commit}) {
        commit("logoutResult");
        return true;
    },

    async goLogin ({ commit }, data) {
        if(data.phone.trim().length  < 10 ||  data.phone.trim().length > 15){
            commit("setAuthError", "Phone rule: Length must be in 10..15");
        }else
        if(data.password.trim().length < 6) {
            commit("setAuthError", "Password length must be larger than 6");
        }else{
            try{
                const result = await authApi.login(data.phone, data.password);
                commit("setAuthResult", {token: result.token, userId: result.user.id});
                commit("setLoading", false);
                return result.user.id;
            }catch(err){
                console.log(err);
                commit("setAuthError", err);
            }
        }
        commit("setLoading", false);
        return false;
    },

    async register({ commit }, data) {
        if(data.username.trim().length < 5 || data.username.trim().length > 15){
            commit("setAuthError", "Username rule: Length must be in 5..15");
        }else
        if(data.phone.trim().length < 10  || data.phone.trim().length > 15){
            commit("setAuthError", "Phone rule: Length must be in 10..15");
        }else
        if(data.password.trim().length < 6) {
            commit("setAuthError", "Password length must be larger than 6");
        }else
        if(data.password != data.rpassword) {
            commit("setAuthError", "Password doesn't match");
        }else{
            try{
                await authApi.register(data.username, data.phone, data.password);
                commit("setLoading", false);
                return true;
            }catch(err){
                commit("setAuthError", err);
            }
        }
        commit("setLoading", false);
        return false;
    }
}
  
  // mutations
const mutations = {

    setAuthResult(state, {token, userId}) {
        state.loading = false;
        state.token = token;
        state.userId = userId;
        state.login = true;
        state.logout = false;
        localStorage.setItem('userId', userId);
        localStorage.setItem('token', token);
    },

    logoutResult(state) {
        state.token = "";
        state.userId = "";
        state.login = false;
        state.logout = true;
        localStorage.setItem('userId', "");
        localStorage.setItem('token', "");
        localStorage.setItem('user', "");
    },

    setAuthError (state, error) {
        state.loading = false;
        state.error = error;
    },

    setLoading(state, value) {
        state.loading = value;
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}