<template>
  <div class="vue-tempalte">
    <!-- Navigation -->
    <nav class="navbar shadow rounded justify-content-between flex-nowrap flex-row fixed-top">
      <div class="container">
        <b-navbar-brand to="/"><span class="white-text">Gamrio</span></b-navbar-brand>
         <div class="nav-item nav-center" v-if="login" center>
              Balance:  <span class="balance">{{ balance }}Z</span>
          </div>  
        <ul class="nav navbar-nav flex-row float-right">
          <li class="nav-item" v-if="logout">
            <router-link class="btn getstart" to="/login"><span class="white-text">Login</span></router-link>
          </li>
          <li class="nav-item" v-if="logout"> 
            <router-link class="btn btn-outline-primary getstart" to="/signup"><span class="white-text">Register</span></router-link>
          </li>
          <li class="nav-item" v-if="login">
               <b-nav-item-dropdown right>
                <!-- Using 'button-content' slot -->
                <template v-slot:button-content class="Â­white-text">
                  <span class="white-text">{{username}} </span><b-avatar variant="primary" :text="name" class="mr-1"></b-avatar>
                </template>
                <b-dropdown-item v-on:click="viewProfile" >Profile</b-dropdown-item>
                <b-dropdown-item >Deposit</b-dropdown-item>
                <b-dropdown-item >Withdraw</b-dropdown-item>
                <b-dropdown-item >Setting</b-dropdown-item>
                <b-dropdown-item v-on:click="doLogout">Sign Out</b-dropdown-item>
              </b-nav-item-dropdown>
           </li> 
        </ul>
      </div>
    </nav>

    <!-- Main -->
    <div class="App">
          <vue-particles color="#dedede" class="app-bg">
        </vue-particles>
        <router-view/>
    </div>
   
     <div v-if="logout">
       <beautiful-chat
      :participants="participants"
      :titleImageUrl="titleImageUrl"
      :onMessageWasSent="onMessageWasSent"
      :messageList="messageList"
      :newMessagesCount="newMessagesCount"
      :isOpen="isChatOpen"
      :close="closeChat"
      :icons="icons"
      :open="openChat"
      :showEmoji="true"
      :showFile="true"
      :showEdition="true"
      :showDeletion="true"
      :showTypingIndicator="showTypingIndicator"
      :showLauncher="true"
      :showCloseButton="true"
      :colors="colors"
      :alwaysScrollToBottom="alwaysScrollToBottom"
      :messageStyling="messageStyling"
      @onType="handleOnType"
      @edit="editMessage" />
     </div>  
  </div>
</template>

<script>
    import { mapState } from 'vuex'
    import CloseIcon from 'vue-beautiful-chat/src/assets/close-icon.png'
    import OpenIcon from 'vue-beautiful-chat/src/assets/logo-no-bg.svg'
    import FileIcon from 'vue-beautiful-chat/src/assets/file.svg'
    import CloseIconSvg from 'vue-beautiful-chat/src/assets/close.svg'

    export default {
        data() {
          return {
            icons:{
              open:{
                img: OpenIcon,
                name: 'default',
              },
              close:{
                img: CloseIcon,
                name: 'default',
              },
              file:{
                img: FileIcon,
                name: 'default',
              },
              closeSvg:{
                img: CloseIconSvg,
                name: 'default',
              },
            },
            participants: [
              {
                id: 'user1',
                name: 'Matteo',
                imageUrl: 'https://avatars3.githubusercontent.com/u/1915989?s=230&v=4'
              },
              {
                id: 'user2',
                name: 'Support',
                imageUrl: 'https://avatars3.githubusercontent.com/u/37018832?s=200&v=4'
              }
            ], // the list of all the participant of the conversation. `name` is the user name, `id` is used to establish the author of a message, `imageUrl` is supposed to be the user avatar.
            titleImageUrl: 'https://a.slack-edge.com/66f9/img/avatars-teams/ava_0001-34.png',
            messageList: [
                { type: 'text', author: `me`, data: { text: `Say yes!` } },
                { type: 'text', author: `user1`, data: { text: `No.` } }
            ], // the list of the messages to show, can be paginated and adjusted dynamically
            newMessagesCount: 0,
            isChatOpen: false, // to determine whether the chat window should be open or closed
            showTypingIndicator: '', // when set to a value matching the participant.id it shows the typing indicator for the specific user
            colors: {
              header: {
                bg: '#4e8cff',
                text: '#ffffff'
              },
              launcher: {
                bg: '#4e8cff'
              },
              messageList: {
                bg: '#ffffff'
              },
              sentMessage: {
                bg: '#4e8cff',
                text: '#ffffff'
              },
              receivedMessage: {
                bg: '#eaeaea',
                text: '#222222'
              },
              userInput: {
                bg: '#f4f7f9',
                text: '#565867'
              }
            }, // specifies the color scheme for the component
            alwaysScrollToBottom: false, // when set to true always scrolls the chat to the bottom when new events are in (new message, user starts typing...)
            messageStyling: true // enables *bold* /emph/ _underline_ and such (more info at github.com/mattezza/msgdown)
          }
        },
        computed: {
            ...mapState({
                login: state => state.auth.login,
                logout: state => state.auth.logout,
                userId: state => state.auth.userId,
                name: state => state.users.name,
                username: state => state.users.username,
                balance: state => state.txgame.balance
            })
        },
        methods: {
            doLogout () {
               this.$store.dispatch("users/goLogout", {id: this.userId})
               .then((res) => {
                 if(res) {
                   this.$store.dispatch("auth/goLogout")
                   .then((res1) => {
                     if(res1){
                       this.$router.push("/login")
                     }
                   })
                 }
               })
            },

            viewProfile() {
              this.$router.push("/profile")
            },

            sendMessage (text) {
              if (text.length > 0) {
                this.newMessagesCount = this.isChatOpen ? this.newMessagesCount : this.newMessagesCount + 1
                this.onMessageWasSent({ author: 'support', type: 'text', data: { text } })
              }
            },
            onMessageWasSent (message) {
              // called when the user sends a message
              this.messageList = [ ...this.messageList, message ]
            },
            openChat () {
              // called when the user clicks on the fab button to open the chat
              this.isChatOpen = true
              this.newMessagesCount = 0
            },
            closeChat () {
              // called when the user clicks on the botton to close the chat
              this.isChatOpen = false
            },
            handleScrollToTop () {
              // called when the user scrolls message list to top
              // leverage pagination for loading another page of messages
            },
            handleOnType () {
              console.log('Emit typing event')
            },
            editMessage(message){
              const m = this.messageList.find(m=>m.id === message.id);
              m.isEdited = true;
              m.data.text = message.data.text;
            }
        }
    }
</script>

<style scoped>
 

    h6 {
        color: #ff0000;
    }
    .navbar{
      text-align: center;
    }
    .nav-center {
      margin:0;
      float:none;
    }
    .balance{
      font-weight: bold;
      font-size: 1em;
      color:#ff0000;
    }
    .getstart{
        width: 100px;
        align-self: center;
        margin-right: 20px;
    }
    .white-text{
      color: #ffffff !important;
      text-decoration:none;
    }
    </style>

