import base from "./base";

const login = (phone, password) => {
    return new Promise((resolve, reject) => {
        base.postHttp({phone: phone, password: password}, "/index/login")
        .then((res) => resolve(res))
        .catch((err) => {
            reject(err);
        });
    });
}

const register = (username, phone, password) => {
    return new Promise((resolve, reject) => {
        base.postHttp({username: username, phone: phone, password: password}, "/index/register")
        .then((res) => resolve(res))
        .catch((err) => {
            reject(err);
        });
    });
}

export default {
    login, 
    register
}