import userApi from '../../api/users';

const state = () => ({
    user: null,
    error: "",
    name: localStorage.getItem("user")? JSON.parse(localStorage.getItem("user")).username.substring(0,1) : "",
    balance: localStorage.getItem("user")? JSON.parse(localStorage.getItem("user")).balance : 0,
    username: localStorage.getItem("user")? JSON.parse(localStorage.getItem("user")).username: "",
    phone: localStorage.getItem("user")? JSON.parse(localStorage.getItem("user")).phone: "",
    adminRole: false,
    userRole: true
})

const getters = {}

const actions = {
    async getUser ({ commit }, data) {
        try{
            const result = await userApi.getUser(data.id);
            commit("setUserResult", result);
            return result.id;
        }catch(err){
            commit("setError", err);
        }
        return false;
    },

    async goLogout({ commit }, data) {
        try{
            const result = await userApi.logout(data.id);
            commit("setLogoutResult", result);
            return true;
        }catch(err){
            commit("setError", err);
        }
        return false;
    }
}
  
  // mutations
const mutations = {
    setUserResult(state, user) {
        state.user = user;
        state.name = user.username.substring(0,1);
        state.username = user.username;
        state.balance = user.balance;
        state.adminRole = user.role == 3;
        state.userRole = user.role == 1;
        localStorage.setItem("user", JSON.stringify(user));
    },

    setLogoutResult(state) {
        state.user = null;
        state.error = "";
        state.name = "";
        state.balance = 0;
    },

    setError (state, error) {
        state.error = error;
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}