import base from "./base";

const getUser = (id) => {
    return new Promise((resolve, reject) => {
        base.getHttp({}, "/users/u/" + id)
            .then((res) => resolve(res))
            .catch((err) => reject(err));
    });
}

const logout = (id) => {
    return new Promise((resolve, reject) => {
        base.deleteHttp("/users/u/" + id)
            .then((res) => resolve(res))
            .catch((err) => reject(err));
    });
}

const loadMessages = () => {
    return new Promise((resolve, reject) => {
        base.getHttp({}, "/users/chats")
            .then((res) => resolve(res))
            .catch((err) => reject(err));
    });
}

export default {
    getUser,
    logout,
    loadMessages
}